import Link from 'next/link'
import NavBar from './navbar'

const Header = () => (
  <header>
    <div className="grid">
      <div className="col-1-1 floatLeft">
        <div id="logo">
        <Link href="/">
          <a>
            <img src="../static/images/pcdLogoText2.png" />
          </a>
        </Link>
        </div>
        <nav id="navigation" className="mainNav">
          <ul className="menu">
            <li>
              <Link href="/about">
                <a className="active">about</a>
              </Link>
            </li>
            <li>
              <Link href="/contact">
                <a>contact</a>
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </header>
)

